import React, { Props } from "react";
import { TopicIconComponent } from "./topicIconComponent";
import { TopicTitleComponent } from "./topicTitleComponent";
import { TopicDescriptionComponent } from "./topicDescriptionComponent";
import "./topicComponent.css";
import {
  Anchor,
  Box,
  Button,
  Card,
  CardBody,
  CardFooter,
  Collapsible,
  Heading,
  Grommet,
  Image,
  Paragraph,
} from 'grommet';
import { Edit, Trash } from 'grommet-icons';
import axios from "axios";
import { Button as SemanticUIButton } from 'semantic-ui-react'
import { Header, Modal, Icon } from 'semantic-ui-react'

export type TopicProps = {
  id: any;
  imagelink: any;
  title: string;
  description: string;
  className: string;
};

const theme = {
  global: {
    font: {
      family: 'Prosto One',
    },
  },
  card: {
    container: {
      background: '#1a3a39',
      elevation: 'none',
    },
    footer: {
      pad: { horizontal: 'medium', vertical: 'small' },
    },
  },
  heading: {
    color: '#ADFF0037',
    level: {
      2: {
        medium: {
          size: "24px",
          height: "30px",
        }
      }
    }
  },
};

export const TopicComponent: (props: TopicProps) => JSX.Element = (
  props: TopicProps
) => {

  return (
      <Grommet theme={theme}>
        <Card elevation="large" width="medium">
          <CardBody height="small">
            <Image
              fit="cover"
              alignSelf="center"
              src={props.imagelink}
            />
          </CardBody>
          <Box pad={{ horizontal: 'medium' }} responsive={false}>
            <Heading level="2" alignSelf="center">
              {props.title}
            </Heading>
            <Paragraph color="white" size="small" margin={{ top: 'none' }}>
              {props.description}
            </Paragraph>
          </Box>
          <CardFooter>
            <Box direction="row" fill align="center" gap="small">
              <Anchor
                href="https://www.collinsdictionary.com/us/dictionary/english/bridge"
                label="MORE"
                size="medium"
                color="#ADFF0037"
              />
              <Box direction="row" fill align="end" alignContent="end" gap="small">
                <Button icon={<Edit />} focusIndicator={false} hoverIndicator onClick={() => { }} />
                <Button icon={<Trash />} focusIndicator={false} hoverIndicator onClick={() => { }} />
              </Box>
            </Box>
          </CardFooter>
        </Card>
      </Grommet>
  );
};

function editComponent(topicId: number, title: string) {
  axios.put('http://185.209.31.106:55558/topic/' + { topicId } + '/edit')
    .then(result => console.log("Отредачили"))
}

function deleteComponent(topicId: number) {
  axios.delete('http://185.209.31.106:55558/topic/' + { topicId } + '/delete')
    .then(result => console.log("Удалили"))
}

